import React, { PureComponent } from 'react';
import Query from '../Query';
import Form from './Form';
import FormGenerator from './FormGenerator';
import { SITE } from '../../graphql/query/remote';
import { CREATE_SITE, UPDATE_SITE } from '../../graphql/mutation/remote';
import { USERS_USERNAME } from '../../graphql/query/remote';
import { getDate } from '../../utils/date';

class CreateSite extends PureComponent {
    render() {
        return (
            <Query query={USERS_USERNAME}>
                {({ loading, error, data }) => {
                    if (loading) {
                        return (<span>loading...</span>);
                    }
                    else if (error) {
                        return (<span>Error Occured!</span>);
                    }
                    const defaults = {
                        name: {
                            type: "text",
                            default: "",
                            required: true,
                        },
                        location: {
                            type: "text",
                            default: "",
                            required: true,
                        },
                        manager: {
                            type: "select",
                            default: data.users,
                            current: null,
                            required: true,
                            validate: val => {
                                return !Boolean(val) ? "No manager selected": null
                            }
                        },
                        createdAt: {
                            type: "date",
                            default: getDate(),
                            required: true
                        }
                    };
                    const { id } = this.props.match.params;
                    return (
                        <Form
                            {...this.props}
                            query={SITE}
                            defaults={defaults}
                            skipOnUpdate={["manager", "createdAt"]}>
                            {({ update, defaults, queryResult }) => {                            
                                return (
                                    <FormGenerator
                                        {...this.props}
                                        defaults={defaults}
                                        mutation={update ? UPDATE_SITE : CREATE_SITE}
                                        title={!update ? "Create a new site" : "Update this site"}
                                        submitText={!update ? "Create site" : "Update site"}
                                        mutateFunc={(fn, data) => { return fn({ variables: { id, data } }); }}
                                    >
                                    </FormGenerator>
                                );
                            }}
                        </Form>
                    );
                    // return (
                    //     <FormGenerator defaults={defaults} submitText="Create Site" mutation={CREATE_SITE} title="Create a new Site" {...this.props}></FormGenerator>
                    // );
                }}
            </Query>
        )
    }
};

export default CreateSite;



